            .btn-order {
                float: none;
                margin: 0 auto;
                width: 60%;
                padding: 10px;
                margin-top: 10px; }
            .btn-register {
                .btn-order {
                    width: 70%; } } } } }

@media (max-width:420px) {
    // PRICES
    .prices {
        .prices-table {
            .currency {
                margin-bottom: 10px; }
            .number-pages {
                span {
                    padding-left: 10px;
                    padding-right: 10px; } } } }
    // ORDER FORM
    .order-form {
        table {
            .checkbox-d {
                td {
                    display: inline-block;
                    span {
                        margin-top: 0;
                        margin-left: 0; }
                    &:first-child {
                        width: 40%; }
                    &:nth-child(2) {
                        float: left; }
                    &:nth-child(3) {
                        display: inline-block;
                        // padding-left: 30px
                        width: 35%; } } }
            .order-now {
                td:nth-child(3) {
                    width: 70%; } } } }
    // BLOG
    .blog {
        .left-info {
            .data {
                .year {
                    display: none; } } }

        .right-blog {
                .continue-reading {
                    .btn-read-more {
                        width: 80%; } } } }
    // SINGLE
    .single {
        .left-info {
            .data {
                .year {
                    display: none; } } }

        .right-blog {
                .continue-reading {
                    .btn-read-more {
                        width: 80%; } } } }
    // SAMPLES
    .samples {
        .sample-item {
            .discount {
                p {
                    font-size: 1rem; } } } } }
