.page-404 {
    padding-bottom: 80px;
    padding-top: 80px;
    .multi-bg {
        width: 100%;
        height: 431px;
        background-image: url(../img/img_boy.png), url(../img/img_books_lamp.png);
        background-position: bottom left, bottom right;
        background-size: 488px 322px, 188px 431px;
        background-repeat: no-repeat, no-repeat; }
    .header-middle-title {
        line-height: 1;
        font-size: 10rem;
        display: block;
        font-weight: 700;
        // float: right
        text-align: center;
        text-transform: uppercase;
        color: $btnColorPrimary; }
    .header-middle-title2 {
        font-size: 3.6rem;
        display: block;
        font-weight: 600;
        text-align: center;
        text-transform: uppercase;
        color: $btnColorPrimary; }
    .header-info {
        font-weight: 300;
        text-align: center;
        // padding-left: 40px
        line-height: 1.3;
        padding-top: 15px;
        p {
            font-size: 2.4rem;
            line-height: 1.5;
            color: $bgColorSecondary; } }
    .btn-discount {
        color: $bgColorSecondary;
        border-color: $bgColorSecondary;
        width: 150px;
        margin: 0 auto;
        padding: 10px 0;
        margin-top: 10px;
        font-weight: 600; }
    .btn-read-more {
        width: 150px;
        margin: 0 auto;
        padding: 10px 0;
        margin-top: 15px; } }
